.label {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 20px;
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 10px;
}

.button {
  padding: 2px 10px;
  margin-right: 10px;
  font-size: 16px;
  font-weight: 500;
  border-radius: 10%;
  background-color: aliceblue;
  border-color: rgb(219, 216, 216);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.button:hover,
.button:focus {
  background-color: rgb(94, 174, 240);
  color: #ffffff;
  cursor: pointer;
}
